import { IGrupoItemListaView, IGrupoItemListaController } from "../../contracts/Cadastros/GrupoItemListaContract";
import axios from "../../plugins/axios"

export class GrupoItemController implements IGrupoItemListaController {

    _view: IGrupoItemListaView

    constructor(view: IGrupoItemListaView) {
        this._view = view
    }
    mudarAprovacao(codigo: any): void {
        this._view.loading(true)
        axios.post('GrupoItem/MudarAprovacao/' + codigo).then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                this.carregarItens()
            } else {
                this._view.showMsg(response.data.message)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

    importItens(): void {
        this._view.loading(true)

        axios.get('GrupoItem/Integracao').then((response: any) => {
            this._view.loading(false)
            if (response.data.sucesso) {
                //this._view.showMsg("Importação completa.")
                this.carregarItens()
            } else {
                this._view.showMsg(response.data.message)
            }
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

    carregarItens(): void {
        this._view.loading(true)

        axios.get('GrupoItem').then((response: any) => {
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            this._view.loading(false)
            this._view.showMsg(e)
        })
    }

}