













































import Vue from "vue";
import KLoading from "../../../components/KLoading.vue";
import KDialog from "../../../components/KDialog.vue";
import KDataList from "../../../components/KDataList.vue";

import { Display } from "../../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../../models/header";
import { Headers } from "../../../models/headers";

import {
  IGrupoItemListaView,
  IGrupoItemListaController
} from "../../../contracts/Cadastros/GrupoItemListaContract";

import { GrupoItemController } from "../../../controllers/Cadastros/GrupoItensListaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class ProdutosLista extends Vue implements IGrupoItemListaView {
  _controller?: IGrupoItemListaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  changeAprovacao(codigo: any) {
    this._controller!.mudarAprovacao(codigo);
  }

  isMobile = false;

  headers = new Headers([
    new Header("Código", "codigo", false, false),
    new Header("Nome", "nome", false, false),
    new Header(
      "Segunda Aprovação?",
      "segundaaprovacao",
      false,
      false,
      "center",
      false
    )
  ]);

  itens = [];

  importLista() {
    this._controller!.importItens();
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new GrupoItemController(this);

    this._controller.carregarItens();
  }
}
